/* General Leaderboard Styling */
.Leaderboard {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: #f5f7fa;
    min-height: 100vh;
}

/* Title */
.Leaderboard h2 {
    font-size: 2.5em;
    color: #333;
    text-align: center;
    margin-bottom: 20px;
}

/* Search bar styling */
.Leaderboard input[type="text"] {
    width: 80%;
    padding: 12px;
    border: 2px solid #ccc;
    border-radius: 10px;
    font-size: 16px;
    margin-bottom: 20px;
    transition: border-color 0.3s;
}

.Leaderboard input[type="text"]:focus {
    border-color: #007bff;
    outline: none;
}

/* Table */
.Leaderboard table {
    border-collapse: collapse;
    width: 90%;
    max-width: 900px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    overflow: hidden;
}

/* Table headers */
.Leaderboard th {
    background-color: #007bff;
    color: #fff;
    padding: 15px;
    cursor: pointer;
    font-weight: bold;
    font-size: 18px;
    text-transform: uppercase;
    transition: background-color 0.3s;
}

.Leaderboard th:hover {
    background-color: #0056b3;
}

/* Table rows */
.Leaderboard td {
    padding: 15px;
    text-align: center;
    font-size: 16px;
    color: #333;
    border-bottom: 1px solid #ddd;
}

.Leaderboard tr:nth-child(even) {
    background-color: #f9f9f9;
}

/* Hover effect for rows */
.Leaderboard tr:hover {
    background-color: #f1f1f1;
    transition: background-color 0.2s;
}

/* Links for player names */
.Leaderboard td a {
    text-decoration: none;
    color: #007bff;
    font-weight: bold;
}

.Leaderboard td a:hover {
    text-decoration: underline;
    color: #0056b3;
}

/* Responsive Design */
@media (max-width: 768px) {
    .Leaderboard th, .Leaderboard td {
        font-size: 14px;
        padding: 10px;
    }

    .Leaderboard input[type="text"] {
        width: 100%;
    }
}
